  function backBtn(e, source) {
    var customTag = document.getElementsByTagName("mojoauth-passwordless-widget")[0];
        var root = customTag.shadowRoot;
        
    source[0].type==='email' && source[0].feature==='magiclink'?root.getElementById('form_tooltip').style.display = "block":source[0].type==='email' && source[0].feature==='otp'?root.getElementById('form_tooltip_otp').style.display = "block":source[0].type==='phone' && source[0].feature==='otp'?root.getElementById('form_tooltip_phone').style.display = "block":root.getElementById('form_tooltip').style.display = "block"
    
    root.getElementById('success_div').style.display = "none";
    root.getElementById('emailotp_success_div').style.display = "none";
    root.getElementById('phoneotp_success_div').style.display = "none";

};


