function setAutoFocus(e: KeyboardEvent, prev: string, next: string, root): void {
	const regex = new RegExp(/^\d+$/);
	if (!regex.test((<HTMLInputElement>e.target).value)) {
		// make value empty if non numeric value entered
		(<HTMLInputElement>e.target).value = '';
	}
	if (prev && (e.code === 'Backspace' || e.code === 'Delete')) {
		(<HTMLInputElement>e.target).blur();
		root.getElementById(prev).focus();
		return;
	}
	if (next && regex.test((<HTMLInputElement>e.target).value)) {
		root.getElementById(next).focus();
	}
}

function createPinBoxes(total: number, root): HTMLDivElement {
	const div = document.createElement('div');
	for (let i = 0; i < total; i++) {
		const inputGroup = document.createElement('div');
		inputGroup.className = 'form-group';
		const input = document.createElement('input');
		input.type = 'text';
		input.maxLength = 1;
		input.className = 'pin_input';
		input.id = `pin${i}`;
		input.inputMode = 'numeric';
		let next = `pin${i + 1}`;
		let prev = `pin${i - 1}`;
		input.addEventListener('keyup', (e: KeyboardEvent) => {
			if (i === total - 1) next = null;
			if (i === 0) prev = null;
			setAutoFocus(e, prev, next, root);
		});

		inputGroup.appendChild(input);
		div.appendChild(inputGroup);
	}
	return div;
}


async function SendMagiclink(apiKey, emailId, lang, redirectUrl) {
    let promise=new Promise(async function(resolve,reject){
		const loginComplete=(response: any)=>{
            
		resolve(response)
	}
    try {
        const emailResponse = await postEmail(apiKey, emailId, lang, redirectUrl);

        if (emailResponse.state_id) {
            ping(apiKey, emailResponse.state_id, loginComplete)
        } else if (emailResponse.code) {
            return emailResponse;
        }
    } catch (error) {
        reject(error)
    }
    })
    return promise
};

